import React from 'react';
import * as Icon from 'react-feather';
import {useTranslation} from 'react-i18next';

function Footer(props) {
  const {t} = useTranslation();

  return (
    <footer className="fadeInUp" style={{animationDelay: '2s'}}>
      <h5>{t('Project made by Nihal Maharaj , Nikhil Rathore , Prashant Namdeo , Abhishek Srivastava')}</h5>

      <div className="link">
        <a
          href="https://github.com/covid19india"
          target="_blank"
          rel="noopener noreferrer"
        >
          covid19india
        </a>
      </div>

      <a
        className="button excel"
        href="http://patientdb.covid19india.org"
        target="_blank"
        rel="noopener noreferrer"
      >
        <Icon.Database />
        <span>{t('Crowdsourced Patient Database')}</span>
      </a>

      <a
        href="https://www.facebook.com/nihal.maharaj.5/"
        target="_blank"
        rel="noopener noreferrer"
        className="button twitter"
        style={{justifyContent: 'center'}}
      >
        
        <Icon.Facebook />
        <span>{t('View updates on Facebook')}</span>
      </a>

    </footer>
  );
}

export default React.memo(Footer);
